export default function App() {
  const [selectedGenre, setGenre] = useState("Movies");
  function genreClickHandler(genre) {
    setGenre(genre);
  }
  return (
    <div className="App">
      <h1> Fun Zone </h1>
      <p> Checkout my favorite Entertainment genre </p>

      <div>
        {Object.keys(funzone).map((genre) => (
          <button onClick={() => genreClickHandler(genre)}>{genre}</button>
        ))}
      </div>
      <div className="blocks">
        <ul className="non-bullet">
          {funzone[selectedGenre].map((fun) => (
            <li className="list" key={fun.name}>
              {" "}
              <div className="names"> {fun.name} </div>
              <div> {fun.rating} </div>
              <div> {fun.description} </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
